import {
  DefaultProps,
  Autocomplete as MantineAutocomplete,
  AutocompleteProps as MantineAutocompleteProps,
  MantineTheme,
} from '@mantine/core';
import { FC, forwardRef } from 'react';
import { TwBaseProps } from '../../types';
import { styled } from '../../utils';

export interface AutocompleteProps
  extends TwBaseProps,
    Omit<MantineAutocompleteProps, keyof DefaultProps> {}

const Root = styled(MantineAutocomplete)<MantineAutocompleteProps>(() => ({}));

export const Autocomplete: FC<AutocompleteProps> = forwardRef((props, ref) => {
  return (
    <Root
      {...props}
      ref={ref}
      styles={(theme: MantineTheme) => {
        return {
          input: {
            borderColor: theme.colors.gray[3],
            minHeight: '38px', // TODO: Keep this here?  Only did this to match Traffic Rules page, but maybe it's best to completely change
            ...(!!props.error && {
              position: 'relative',
              top: theme.spacing.xs,
            }),
          },
          error: {
            position: 'relative',
            top: theme.spacing.xs,
            paddingTop: 5,
          },
          icon: {
            paddingTop: !!props.error ? theme.spacing.lg : '',
          },
        };
      }}
    />
  );
});
